import React, { useEffect, useState } from 'react';
import { Line } from 'react-chartjs-2';
import { initializeApp } from 'firebase/app';
import { getDatabase, ref, onValue, off } from 'firebase/database';
import Chart from 'chart.js/auto';
import './EmptyChart.css';

// Your web app's Firebase configuration
const firebaseConfig = {
  apiKey: "AIzaSyCTE_wM9n2TC_NyRkIfZ5t3OJo380AnwS8",
  authDomain: "nilm-database-de335.firebaseapp.com",
  databaseURL: "https://nilm-database-de335-default-rtdb.firebaseio.com",
  projectId: "nilm-database-de335",
  storageBucket: "nilm-database-de335.appspot.com",
  messagingSenderId: "337277772529",
  appId: "1:337277772529:web:73fa027fea48923e7aff50"
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);

const EmptyChart = ({ appliance }) => {
  const [data, setData] = useState({});
  const [selectedTimeFrame, setSelectedTimeFrame] = useState('1min'); // Default to show all data
  const [liveReading, setLiveReading] = useState(0);
  const [livePrice, setLivePrice] = useState(1);

useEffect(() => {
  const database = getDatabase(app);
  let dataPath;
  switch (selectedTimeFrame) {
    case '1min':
      dataPath = '/data/1min';
      break;
    case '3min':
      dataPath = '/data/3min';
      break;
    case '5min':
      dataPath = '/data/5min';
      break;
    default:
      dataPath = '/data/All-Time';
  }
  const dataRef = ref(database, dataPath);

  const onDataChange = (snapshot) => {
    const newData = snapshot.val();
    Object.entries(newData).forEach(([timestamp, value], index, array) => {
      if (index > 0) {
        const previousValue = array[index-1][1];
        const currentValue = value
        const change = currentValue - previousValue;
        if (Math.abs(change) >= 70) {
          const database = getDatabase(app);
          const applianceRef = ref(database, `stampedAppliances/${appliance}`);
          onValue(applianceRef, (snapshot) => {
            const applianceData = snapshot.val();
            const changeWithinRange = Object.values(applianceData).some((value) => Math.abs(change - value) <= 10);
            // Update the data accordingly
            if (!changeWithinRange) {
              array[index][1] = previousValue;
              newData[timestamp] = previousValue;
            }
          });
        }
      }
    });
 
            let sum = 0;
            let cost = 0;
            Object.values(newData).forEach(value => {
              sum += (1 / 3600) * value;
              cost = sum * (0.15 / 1000);
            });
            setLiveReading(sum.toFixed(2)); // Update live reading state
            setLivePrice(cost.toFixed(6));

    setData(newData);
  };

  // Set up the data listener
  onValue(dataRef, onDataChange);

  // Cleanup function to remove the listener when the component unmounts
  return () => {
    off(dataRef, 'value', onDataChange);
  };
}, [selectedTimeFrame]);

  const chartData = {
    labels: Object.keys(data),
    datasets: [
      {
        label: 'Apparent Power Data',
        data: Object.values(data),
        fill: false,
        borderColor: 'rgba(75,192,192,1)',
        borderWidth: 2,
      },
    ],
  };

  
  return (
        <div className="emptychart-intro-container" style={{ background: "black" }}>
            <div className="emptychart-container-left">
                {/* Content for the left container */}
                <div className="graphcontainer" style={{background:"lightgrey"}}>
                    <h1>Real Time Apparent Power</h1>
                    <h1 style={{marginTop:"-20px"}}>Signal for: {appliance}</h1>  
                    <select value={selectedTimeFrame} onChange={(e) => setSelectedTimeFrame(e.target.value)}>
                      <option value="all">All Time</option>
                      <option value="1min">Last 60 Entries (Ideally Last 1 Minute)</option>
                      <option value="3min">Last 180 Entries (Ideally Last 3 Minutes)</option>
                      <option value="5min">Last 300 Entries (Ideally Last 5 Minutes)</option>
                    </select>
                      <Line data={chartData} />
                </div>
            </div>
            <div className="emptychart-container-right" style={{background:"black"}}>
                {/* Content for the right container */}
                <div>
                    {/* Content for the left container */}
                <p className="title" style={{color:"white", marginTop:"10px",fontSize:"40px"}}>
                    {appliance} Reading:
                </p>
                <div style={{background:"white", maxWidth:"260px", marginTop:"-40px"}}>
                    <p className="title" style={{color:"black", border: "10px solid black", background:"lightblue"}}>
                        {liveReading} Wh
                    </p>
                </div>
                <p className="title" style={{color:"white", marginTop:"-40px", fontSize:"40px"}}>
                    {appliance} Usage Cost:
                </p>
                <p className="title2" style = {{fontSize:"20px", marginTop:"-25px"}}>
                    Assuming rate of 15 /kWh
                </p>
                <div style={{background:"white", maxWidth:"360px", marginTop:"-40px"}}>
                    <p className="title" style={{color:"black", border: "10px solid black", background:"lightblue"}}>
                       $ {livePrice}  
                    </p>
                </div>
                </div>
            </div>
        </div>
    )
};

export default EmptyChart;
