import React, { useState, useEffect} from "react";
import "./KWandTMN.css"; // Import CSS file for styling
import { initializeApp } from 'firebase/app';
import { getDatabase, ref, onValue, off } from 'firebase/database';

// Your web app's Firebase configuration
const firebaseConfig = {
  apiKey: "AIzaSyCTE_wM9n2TC_NyRkIfZ5t3OJo380AnwS8",
  authDomain: "nilm-database-de335.firebaseapp.com",
  databaseURL: "https://nilm-database-de335-default-rtdb.firebaseio.com",
  projectId: "nilm-database-de335",
  storageBucket: "nilm-database-de335.appspot.com",
  messagingSenderId: "337277772529",
  appId: "1:337277772529:web:73fa027fea48923e7aff50"
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);

function KWandTMN() {
    const database = getDatabase(app);
    const dataRef = ref(database, '/data/All-Time');
    const [liveReading, setLiveReading] = useState(0);
    const [livePrice, setLivePrice] = useState(1);
    const [thresholdValue, setThresholdValue] = useState(2); // Default threshold value
    const [recipientNumber, setRecipientNumber] = useState('');

    useEffect(() => {
        const database = getDatabase(app);
        const dataRef = ref(database, '/data/All-Time');

        const handleData = (snapshot) => {
            const newData = snapshot.val();
            let sum = 0;
            let cost = 0;
            Object.values(newData).forEach(value => {
                sum += (1 / 3600) * value;
                cost = sum * (0.15 / 1000);
            });
            setLiveReading(sum.toFixed(2)); // Update live reading state
            setLivePrice(cost.toFixed(6));
        };

        const dataListener = onValue(dataRef, handleData);

        // Clean up listener when component unmounts
        return () => {
            off(dataRef, dataListener);
        };
    }, [app]);

    const handleThresholdChange = (event) => {
        // Update threshold value state when input value changes
        setThresholdValue(parseFloat(event.target.value));
        console.log("something changed");
    };

  const handleRecipientChange = (event) => {
    setRecipientNumber(event.target.value);
  };

    return (
        <div className="intro-container" style={{background:"black"}}>
            <div className="container-left" style={{background:"green"}}>
                {/* Content for the left container */}
                <p className="title" style={{color:"white", marginTop:"10px"}}>
                    Live Meter Reading:
                </p>
                <div style={{background:"white", maxWidth:"260px", marginTop:"-40px"}}>
                    <p className="title" style={{color:"black", border: "10px solid black"}}>
                        {liveReading} Wh
                    </p>
                </div>
                <p className="title" style={{color:"white", marginTop:"-40px"}}>
                    Total Usage Cost:
                </p>
                <p className="title2" style = {{fontSize:"20px", marginTop:"-50px"}}>
                    Assuming rate of 15 /kWh
                </p>
                <div style={{background:"white", maxWidth:"360px", marginTop:"-40px"}}>
                    <p className="title" style={{color:"black", border: "10px solid black"}}>
                       $ {livePrice}  
                    </p>
                </div>
            </div>
            <div className="container-right" style={{background:"black"}}>
                {/* Content for the right container */}
                <p className="title2" style = {{fontSize:"50px"}}>
                    Set Threshold Value:
                </p>
                <p className="title2" style = {{fontSize:"20px", marginTop:"-50px"}}>
                    Default value is set to: 2 Wh
                </p>
                <div style={{marginTop:"0px"}}>
                    <input 
                        type="number"  
                        value={thresholdValue}
                        onChange={handleThresholdChange}
                        style={{ fontSize: "39px", maxWidth:"70px", height:"40px"}}
                    />
                </div>

                <p className="title2" style = {{fontSize:"40px"}}>
                    Enter Phone Number for Text-Message Notifications:
                </p>
                <p className="title2" style = {{fontSize:"20px", marginTop:"-30px"}}>
                    Standard data rates may apply.
                </p>

                <div style={{marginTop:"-40px"}}>
                    <input 
                        type="text" 
                        placeholder="Enter Phone Number" 
                        value={recipientNumber}
                        onChange={handleRecipientChange}
                    />
                    <button >Set</button>
                </div>
            </div>
        </div>
    )
}


export default KWandTMN;
