import React, { useState, useEffect } from 'react';
import './navBar.css';

const NavigationBar = () => {
  const [showNavbar, setShowNavbar] = useState(true);

  // Function to toggle the visibility of the navbar based on screen width
  const handleNavbarVisibility = () => {
    setShowNavbar(window.innerWidth >= 700); // Show navbar if screen width is 700px or larger
  };

  // Add event listener to handle window resize
  useEffect(() => {
    handleNavbarVisibility(); // Initial visibility check
    window.addEventListener('resize', handleNavbarVisibility);

    return () => {
      window.removeEventListener('resize', handleNavbarVisibility);
    };
  }, []); // Only run on component mount and unmount

  const handleScroll = (scrollOffset) => {
    window.scrollTo({
      top: scrollOffset,
      behavior: 'smooth'
    });
  };

  return (
    <nav className="navbar" style={{ display: showNavbar ? 'block' : 'none' }}>
      <div className="navbar-container">
                <button onClick={() => handleScroll(0)} style={{color:"white"}}>Add Appliance</button>
        <button onClick={() => handleScroll(650)} style={{color:"white"}}>Aggregated Power Charts</button>
        <button onClick={() => handleScroll(1240)} style={{color:"white"}}>Meter Reading / SMS Notifs</button>
        <button onClick={() => handleScroll(1900)} style={{color:"white"}}>Model Plot</button>
        <button onClick={() => handleScroll(2750)} style={{color:"white"}}>Appliance Signals/Readings</button>
      </div>
    </nav>
  );
};

export default NavigationBar;
