import React, { useEffect, useState } from 'react';
import { Line } from 'react-chartjs-2';
import { initializeApp } from 'firebase/app';
import { getDatabase, ref, onValue, off } from 'firebase/database';
import KMeans from 'kmeans-js';

const firebaseConfig = {
    apiKey: "AIzaSyCTE_wM9n2TC_NyRkIfZ5t3OJo380AnwS8",
  authDomain: "nilm-database-de335.firebaseapp.com",
  databaseURL: "https://nilm-database-de335-default-rtdb.firebaseio.com",
  projectId: "nilm-database-de335",
  storageBucket: "nilm-database-de335.appspot.com",
  messagingSenderId: "337277772529",
  appId: "1:337277772529:web:73fa027fea48923e7aff50"// Your Firebase configuration
};

const app = initializeApp(firebaseConfig);

const EmptyChart = ({ appliance }) => {
  const [data, setData] = useState({});
  const [selectedTimeFrame, setSelectedTimeFrame] = useState('1min');

  useEffect(() => {
    const database = getDatabase(app);
    let dataPath;
    switch (selectedTimeFrame) {
      case '1min':
        dataPath = '/data/1min';
        break;
      case '3min':
        dataPath = '/data/3min';
        break;
      case '5min':
        dataPath = '/data/5min';
        break;
      default:
        dataPath = '/data/All-Time';
    }
    const dataRef = ref(database, dataPath);

    const onDataChange = (snapshot) => {
  const newData = snapshot.val();
  if (newData && typeof newData === 'object') {
    const values = Object.values(newData);
    if (Array.isArray(values)) {
      const features = values.map(value => [value]);
      const kmeans = new KMeans({ K: 2 });
      const clusters = kmeans.cluster(features);
      console.log("Clusters:", clusters);

      // Check if clusters are defined and clusters.clusters is an array
      if (clusters && Array.isArray(clusters.clusters)) {
        // Update chart data based on clusters
        const newDataWithLabels = {};
        clusters.clusters.forEach((cluster, index) => {
          const label = cluster.includes(0) ? 'Hair Dryer' : 'Heat Gun';
          cluster.forEach((pointIndex) => {
            const timestamp = Object.keys(newData)[pointIndex];
            newDataWithLabels[timestamp] = { value: newData[timestamp], label };
          });
        });
        setData(newDataWithLabels);
      } else {
        console.error("Error processing clusters:", clusters);
      }
    } else {
      console.error("Data retrieved from Firebase is not an array:", values);
    }
  } else {
    console.error("No valid data retrieved from Firebase:", newData);
  }
};

    onValue(dataRef, onDataChange);

    return () => {
      off(dataRef, 'value', onDataChange);
    };
  }, [selectedTimeFrame]);

  const chartData = {
  labels: Object.keys(data),
  datasets: [
    {
      label: 'Apparent Power Data',
      data: Object.values(data).map(entry => entry.value), // Use the 'value' property for data
      fill: false,
      borderColor: 'rgba(75,192,192,1)',
      borderWidth: 2,
    },
  ],    
};

  return (
    <div>
      <h1>Real Time Apparent Power Signal for: {appliance}</h1>
      <select value={selectedTimeFrame} onChange={(e) => setSelectedTimeFrame(e.target.value)}>
        <option value="all">All Time</option>
        <option value="1min">Last 60 Entries (Ideally Last 1 Minute)</option>
        <option value="3min">Last 180 Entries (Ideally Last 3 Minutes)</option>
        <option value="5min">Last 300 Entries (Ideally Last 5 Minutes)</option>
      </select>
      <Line data={chartData} />
    </div>
  );
};

export default EmptyChart;
